.button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 56px;
  border-radius: 40px;
  background-color: #fff;

  text-decoration: none;
  font-weight: 700;
  font-size: 16px;
  line-height: 27px;
  color: $mainDarkColor;

  &:hover {
    box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.4);
    cursor: pointer;
  }

  &:active {
    box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.64);
  }
}
